import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProjectMainComponent } from './project-main/project-main.component';
import {RouterModule, Routes} from "@angular/router";
import {ShareModule} from "../shared/share.module";
import {ServiceModule} from "../services/service.module";
import { ProjectSidebarComponent } from './project-main/project-sidebar/project-sidebar.component';

const routes: Routes = [
  {path: 'project', children: [
      {path: ':name', component: ProjectMainComponent },
      {path: '', component: ProjectMainComponent, pathMatch: 'full'},
      {path: '**', component: ProjectMainComponent},
    ]
  }
];

@NgModule({
  imports: [
    ServiceModule,
    ShareModule,
    CommonModule,
    RouterModule.forChild(routes),
  ],
  declarations: [
    ProjectMainComponent,
    ProjectSidebarComponent,
  ],
  exports:[
    ProjectMainComponent,
    RouterModule
  ],
})
export class ProjectModule { }



