import {EventEmitter, Injectable} from "@angular/core";
import {RestDatasourceService} from "./rest-datasource.servics";
import {Project} from "../models/project.model";
import {Observable} from "rxjs/Observable";
import {ProjectSummary} from "../models/project-summary.model";


@Injectable()
export class ProjectRepository {
  private _projects: Project[] = [];
  private _currentProject: Project;
  public changeCurrentProjectEmitter: EventEmitter<Project> = new EventEmitter<Project>();

  constructor(private dataSource: RestDatasourceService) {
    this.dataSource.get('project').subscribe(data => {
      this._projects = data;
      console.log(this._projects);
    });
  }

  set currentProject(project: Project) {
    this.changeCurrentProjectEmitter.emit(project);
    this._currentProject = project;
  }

  get currentProject(){
    return this._currentProject;
  }

  getProject(id: number): Project{
    return this._projects.find(_project => _project.id == id);
  }

  getProjects(): Observable<Project[]> {
    return this.dataSource.get('project');
  }


  getProjectByName(name: string): Project {
    return this._projects
      .filter(pro => pro.name.toLowerCase() == name.toLowerCase())[0];
  }

  getProjectSummary(projectId: number): Observable<ProjectSummary> {
    return this.dataSource.get(`project/summary/${projectId}`);
  }

  getProjectSummaryByName(projectName: string): Observable<ProjectSummary> {
    let id: number = this._projects.filter(_project => _project.name == projectName).map(_project => _project.id)[0];
    return this.getProjectSummary(id);
  }


}



