import {Component, OnInit, ViewChild} from '@angular/core';
import {Project} from "../../models/project.model";
import {RestDatasourceService} from "../../services/rest-datasource.servics";
import {ProjectRepository} from "../../services/project-repository.service";
import {ActivatedRoute} from "@angular/router";
declare var $: any;

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit{
  projects: Project[] = [];

  constructor(public projectRepository: ProjectRepository,
              private route: ActivatedRoute) {
    $(document)
      .ready(function() {
        $('.ui.menu .ui.dropdown').dropdown({
          on: 'hover'
        });
        $('.ui.menu a.item')
          .on('click', function() {
            $(this)
              .addClass('active')
              .siblings()
              .removeClass('active')
            ;
          })
        ;
      });
    this.projectRepository.getProjects().subscribe(data => {
      this.projects = data;
    });
  }
  ngOnInit(): void {

  }

  getProject(name: string) {
    return this.projectRepository.getProjectByName(name);
  }

}



