import {RestDatasourceService} from "./rest-datasource.servics";
import {DocumentRepository} from "./document-repository.service";
import {ProjectRepository} from "./project-repository.service";
import {SectionRepository} from "./section-repository.service";


@NgModule({
  providers: [
    RestDatasourceService,
    /*  App Repository  */
    DocumentRepository,
    ProjectRepository,
    SectionRepository,
  ],
})
export class ServiceModule{}



