import { Component, OnInit } from '@angular/core';
import {ProjectRepository} from "../../services/project-repository.service";
import {Project} from "../../models/project.model";
import {ActivatedRoute, ActivatedRouteSnapshot} from "@angular/router";
import {Section} from "../../models/section.model";
import {SectionRepository} from "../../services/section-repository.service";
import {ProjectSummary} from "../../models/project-summary.model";
import {Chapter} from "../../models/chapter.model";

@Component({
  selector: 'app-project-main',
  templateUrl: './project-main.component.html',
  styleUrls: ['./project-main.component.css']
})
export class ProjectMainComponent implements OnInit {
  project: Project;
  projectSummary: ProjectSummary;
  chapters: Chapter[] = [];


  constructor(private productRepository: ProjectRepository,
              private sectionRepository: SectionRepository,
              private route: ActivatedRoute) {
    this.route.params.subscribe(params => {
      let projectName = params['name'];
      this.project = this.productRepository.getProjectByName(projectName);
      this.productRepository.currentProject = this.project;
    });
    this.productRepository.changeCurrentProjectEmitter.subscribe(project => this.init(project));
  }

  ngOnInit() {

  }

  init(project: Project) {
    this.project = project;
    this.productRepository.getProjectSummary(this.project.id).subscribe(data => {
      this.projectSummary = data;
      this.chapters = [];
      let sectionChapter: Chapter[] = [];
      for(let sectionName of this.projectSummary.sectionNames) {
        sectionChapter.push(new Chapter(sectionName,''));
      }
      this.chapters.push(new Chapter(this.project.name,this.project.description, sectionChapter));
    });
  }


  getDocumentTitleByName(sectionName: string): string[] {
    let titles: string[] = [];
    for(let _sectionName of Object.keys(this.projectSummary.documentTitles)){
      if(_sectionName == sectionName) {
        titles = this.projectSummary.documentTitles[_sectionName];
        console.log(titles);
      }
    }
    return titles;
  }

}



