import {NgModule} from "@angular/core";
import { FooterComponent } from './footer/footer.component';
import { BottomComponent } from './bottom/bottom.component';
import { HeaderComponent } from './header/header.component';
import {CommonModule} from "@angular/common";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import { LocationComponent } from './location/location.component';
import { MainComponent } from './main/main.component';
import {MatButtonModule, MatFormFieldModule, MatInputModule, MatMenuModule, MatSelectModule} from "@angular/material";
import {RouterModule} from "@angular/router";
import {ListChapterComponent} from "./components/list-chapter/list-chapter.component";
import {ListChapterItemComponent} from "./components/list-chapter/list-chapter-item/list-chapter-item.component";
import { DataLoaderComponent } from './components/data-loader/data-loader.component';

@NgModule({
  declarations: [
    FooterComponent,
    BottomComponent,
    HeaderComponent,
    LocationComponent,
    MainComponent,
    ListChapterComponent,
    ListChapterItemComponent,
    DataLoaderComponent,
  ],
  imports : [
    MatButtonModule,
    MatInputModule,
    MatFormFieldModule,
    MatSelectModule,
    MatMenuModule,
    CommonModule,
    BrowserAnimationsModule,
    RouterModule,
  ],
  exports : [
    FooterComponent,
    BottomComponent,
    HeaderComponent,
    LocationComponent,
    ListChapterComponent,
    ListChapterItemComponent,
    DataLoaderComponent,
    MatInputModule,
    MatFormFieldModule,
    MatSelectModule,
    MatButtonModule,
    MatMenuModule,
  ]
})
export class ShareModule{ }



